.covid-container {
    padding: 10px 0;
    display: none;

    @media screen and (min-width: 375px) {
        display: block;
    }

    h2 {
        text-align: center;
        margin: 5px 0;
    }

    h5 {
        text-align: center;
        margin: 5px 0 15px 0;
    }

    .totals {
        text-align: center;
        padding: 20px 0;

        &__lastupdated {
            font-size: 11px;
            margin-bottom: 15px;
            font-weight: 500;
            color: darkblue;
        }

        &__elem {
            display: inline-block;
            margin-right: 30px;
        }

        &__confirmed {
            color: rgb(66, 133, 244);
        }

        &__active {
            color: rgb(103, 174, 63);
        }

        &__recovered {
            color: orange;
        }

        &__deaths {
            margin-right: 0;
            color: red;
        }

        .label {
            font-size: 13px;

            @media screen and (min-width: 1024px) {
                font-size: 16px;
            }
        }

        .count {
            font-size: 12px;

            @media screen and (min-width: 1024px) {
                font-size: 14px;
            }
        }
    }
}
